import React, { Fragment } from 'react';
import {
  Col, Container, Navbar, Row,
} from 'react-bootstrap';
import '../styles/footer.css';
import ContentLine from './contentLine';


const style = {
  padding: '7rem 0',
  backgroundColor: '#e9ecef',
};


function BottomText({ text }) {
  return (
    <ul className="footer-list text-small">
      {
        text.map(el => (
          <li>
            {
              el.href ? <a href={el.href} className="text-muted">{el.text}</a> : (
                <text className="text-muted">{el.text}</text>
              )
            }
          </li>
        ))
      }
    </ul>
  );
}

export default ({ footerData }) => (
  <footer className="pt-4 my-md-5 pt-md-5 border-top">
    <Row>
      {
        footerData.map(el => (
          <Col>
            <h4>{el.name}</h4>
            <BottomText text={el.text} />
          </Col>
        ))
      }
    </Row>
  </footer>
);
